export default {
  // 
  namespace: 'XPE',
  // UI
  UI: {
    // 
    header: {
      // true:  false: 
      enable: true,
      // 
      title: {
        enable: true,
        text: 'XPE',
        fullText: 'X-Page-Editor',
        subText: 'x-page-editor'
      },
      // 
      data: [],
      // 
      initData: []
    },
    // 
    list: {
      // true:  false: 
      enable: true,
      // 
      data: [],
      // 
      initData: [],
      // 
      style: {
        // width  margin-left 
        width: '400px',
        'margin-left': '-400px'
      }
    },
    // 
    board: {
      // true:  false: 
      enable: true,
      // 
      data: [],
      // 
      initData: [],
      // 
      toolBar: {
        // true:  false: 
        enable: true,
        //  top-left || top-right || bottom-right || bottom-left
        position: 'top-right',
        style: {
          right: '450px'
        }
      },
      // 
      scale: {
        // true:  false: 
        enable: true
      }
    },
    // 
    options: {
      // true:  false: 
      enable: true,
      // 
      data: [],
      // 
      initData: [],
      // 
      style: {
        // width  margin-right 
        width: '400px',
        'margin-right': '-400px'
      }
    },
    // 
    footer: {
      // true:  false: 
      enable: true,
      // 
      data: [],
      // 
      initData: []
    }
  },
  // 
  output: {
    // 
    before: () => {},
    after: () => {}
  }
}



