*
* example 
*/

<style scoped lang="less" rel="stylesheet/less">
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }
  .page-editor-example {
    width: 100%;
    height: 800px;
  }
</style>

<template>
  <div id="app">
    <a href="https://github.com/OXOYO/X-Page-Editor-Vue" target="_blank">
      <img
        style="position: absolute; top: 0; right: 0; border: 0; z-index: 5000"
        src="https://s3.amazonaws.com/github/ribbons/forkme_right_orange_ff7600.png"
        alt="Fork me on GitHub"
      >
    </a>
    <!--<img src="./assets/logo.png">-->
    <div class="page-editor-example">
      <XPageEditor :config="pageEditorConfig">
        <!--  -->
        <!--<CustomList slot="list"></CustomList>-->
        <CustomListItem slot="listItem"></CustomListItem>
      </XPageEditor>
    </div>
  </div>
</template>

<script>
import CustomList from './components/CustomList.vue'
import CustomListItem from './components/ListItem.vue'
import Resources from './resources'

export default {
  name: 'App',
  components: {
    CustomList,
    CustomListItem
  },
  data () {
    return {
      pageEditorConfig: {
        // UI
        UI: {
          // 
          header: {
            // true:  false: 
            enable: true,
            // 
            title: {
              enable: true,
              text: 'XPE',
              fullText: 'X-Page-Editor',
              subText: 'x-page-editor'
            },
            // 
            data: [],
            // 
            initData: []
          },
          // 
          list: {
            // true:  false: 
            enable: true,
            // 
            data: Resources,
            // 
            initData: [],
            style: {
              width: '300px',
              'margin-left': '-300px'
            }
          },
          // 
          board: {
            // true:  false: 
            enable: true,
            // 
            data: [],
            // 
            initData: [],
            // 
            toolBar: {
              // true:  false: 
              enable: true,
              //  top-left || top-right || bottom-right || bottom-left
              position: 'top-right',
              style: {
                right: '350px'
              }
            },
            // 
            scale: {
              // true:  false: 
              enable: true
            }
          },
          // 
          options: {
            // true:  false: 
            enable: true,
            // 
            data: [],
            // 
            initData: [],
            style: {
              width: '300px',
              'margin-right': '-300px'
            }
          },
          // 
          footer: {
            // true:  false: 
            enable: true,
            // 
            data: [],
            // 
            initData: []
          }
        },
        // 
        output: {
          // 
          before: () => {},
          after: () => {}
        }
      }
    }
  }
}
</script>



