<style lang="less" rel="stylesheet/less">
  .xpe_add-project-modal {
    text-align: left;
  }
</style>

<template>
  <XUIModal
    class="xpe_add-project-modal"
    :value="isShow"
    title=""
    :transfer="false"
    @on-ok="handleOK"
    @on-cancel="handleCancel"
  >
    <XUIForm
      ref="addProjectForm"
      :model="formData"
      :rules="formRules"
      :label-width="120"
      @keydown.native.enter.prevent
    >
      <XUIFormItem label="" prop="projectName">
        <XUIInput type="text" v-model="formData.projectName" placeholder="" style="width: 300px;"></XUIInput>
      </XUIFormItem>
    </XUIForm>
    <div slot="footer">
      <Button type="text" size="large" @click="handleCancel"></Button>
      <Button type="primary" size="large" @click="handleOK"></Button>
    </div>
  </XUIModal>
</template>

<script>
import utils from '@/global/utils'

export default {
  name: 'XPEAddProjectModal',
  data () {
    return {
      // 
      isShow: false,
      // 
      formData: {
        projectName: ''
      },
      // 
      formRules: {
        projectName: {
          required: true,
          trigger: 'change'
        }
      }
    }
  },
  methods: {
    handleOK: function () {
      let _t = this
      // 
      let validResult = false
      _t.$refs['addProjectForm'].validate((valid) => {
        validResult = valid
        if (!valid) {
          _t.$Message.error('')
        }
      })
      console.log('validResult', validResult)
      if (!validResult) {
        return false
      }
      // 
      _t.isShow = false
      let timeNow = new Date().getTime()
      let payload = {
        id: 'project-' + timeNow,
        name: _t.formData.projectName,
        type: 'pc',
        components: [],
        selectionStyleMap: {}
      }
      // 
      utils.bus.$emit('XPE/project/add/ok', payload)
      _t.$nextTick(function () {
        // 
        _t.formData = {
          projectName: ''
        }
      })
    },
    handleCancel: function () {
      let _t = this
      // 
      _t.isShow = false
    }
  },
  created: function () {
    let _t = this
    // 
    utils.bus.$on('XPE/project/add', function () {
      _t.isShow = true
    })
  }
}
</script>



